  text-align: center;
  display: grid;
}

@media (min-width: 768px) {
  main {
    grid-template-columns: auto auto;
  }
} */

.color-name {
    text-align: center;
    white-space: nowrap;
    transition: 0.3s;
}

.color-name:hover,
.color-name.success {
    color: hsl(0, 0%, 99%);
}

.color-name:hover {
    cursor: pointer;
}

.nav {
    padding: 1rem;
    background: hsl(300, 5%, 10%);
    position: fixed;
    inset: 0 0 auto 0;
}

.nav.scrolled {
    border-bottom: 1px solid hsl(0, 0%, 50%);
}

.menu {
    padding: 0;
    margin: 0;
    list-style-type: none;
    display: flex;
    justify-content: flex-start;
    align-content: center;
    flex-wrap: wrap;
}

.menu-item {
    margin-right: 2rem;
    line-height: 1;
    display: flex;
    align-items: center;
}

.menu-item:last-of-type {
    margin-left: auto;
}

.menu-item .swatch {
    display: inline-block;
    margin-left: 0.5rem;
    height: 2rem;
    width: 2rem;
}

@media (max-width: 767px) {
    .menu {
        justify-content: space-between;
    }

    .menu-item {
        margin: 0 1rem 1rem;
    }

    .menu-item:last-of-type {
        margin: 0 1rem;
        font-size: 0;
    }
}
.header {
    text-align: center;
}

.title {
    line-height: 1.2;
}

.subtitle {
    margin: 1rem auto 2rem;
    line-height: 1.2;
}

.color-window {
    margin: auto auto 1rem;
    height: 200px;
    width: 100%;
}

.text-block {
    margin: 2rem auto;
}

.text-block p {
    margin-bottom: 1rem;
}
.preview-window {
    padding: 1rem;
    text-align: left;
    font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, segoe ui, helvetica neue, helvetica, Ubuntu, roboto, noto, arial, sans-serif;
    border: 1px solid hsl(0, 0%, 30%);
}

.preview-window h2,
.preview-window p {
    margin: 0 0 0.5rem;
    color: inherit;
}

.preview-window h2 {
    line-height: 1.2;
}

.preview-window p {
    font-weight: 400;
}
#Preview {
    padding-top: 3rem;
    margin: 0;
}

.text-label,
.text-input {
    max-width: 400px;
    display: block;
    text-align: left;
}

.text-label {
    margin-top: 1.5rem;
    color:hsl(0, 0%, 99%);
}

.text-input {
    padding: 0.5rem 0;
    background: transparent;
    width: 100%;
    color: hsl(0, 0%, 99%);
    font-size: 20px;
    font-weight: 700;
    font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, segoe ui, helvetica neue, helvetica, Ubuntu, roboto, noto, arial, sans-serif;
    border: none;
    border-bottom: 2px solid hsl(0, 0%, 70%);
    border-radius: 0;
    outline: none;
}

.text-input:focus {
    background: transparent;
}

.section-wrap {
    margin-top: 3rem;
}

.section-wrap section {
    margin-top: 3rem;
}

@media (min-width: 768px) {
    .section-wrap {
        display: grid;
        grid-template-columns: auto auto;
        grid-gap: 3rem;
        gap: 3rem;
    }

    .section-wrap section {
        margin-top: 0;
    }
}

.description {
    text-align: center;
}
.footer {
    margin-top: 4rem;
    text-align: center;
}
.slider ~ .slider {
   margin-top: 1rem;
}

.slider-label {
  font-weight: 700;
  width: 75px;
  text-align: left;
}

.slider-input {
  margin: 0;
  flex: 1 1;
  max-width: 360px;
}

@media (min-width: 400px) {
  .slider {
      display: flex;
      justify-content: center;
  }

  .slider ~ .slider {
    margin-top: 0.75rem;
  }
}

input[type=range] {
  box-sizing: border-box;
  font-size: 16px;
  line-height: 1;
  height: 1.25em;
  background-color: transparent;
  cursor: pointer;
  -webkit-appearance: none;
  width: 100%;
}
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-ms-track {
  width: 100%;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 1.5em;
  height: 1.5em;
  margin-top: 0;
  background-color: hsl(0, 0%, 99%);
  border-radius: 1em;
  border: 2px solid rgba(255, 255, 255, 0.5);
  cursor: pointer;
}
input[type=range]::-moz-range-thumb {
  width: 1.5em;
  height: 1.5em;
  margin-top: 0;
  background-color: hsl(0, 0%, 99%);
  border-radius: 1em;
  border: 2px solid rgba(255, 255, 255, 0.5);
  cursor: pointer;
}
input[type=range]::-ms-thumb {
  width: 1.5em;
  height: 1.5em;
  margin-top: 0;
  background-color: hsl(0, 0%, 99%);
  border-radius: 1em;
  border: 2px solid rgba(255, 255, 255, 0.5);
  cursor: pointer;
}
input[type=range]:hover::-webkit-slider-thumb {
  border-color: rgba(255, 255, 255, 0.7);
}
input[type=range]:hover::-moz-range-thumb {
  border-color: rgba(255, 255, 255, 0.7);
}
input[type=range]:hover::-ms-thumb {
  border-color: rgba(255, 255, 255, 0.7);
}
input[type=range]:active::-webkit-slider-thumb {
  border-color: #ffffff;
}
input[type=range]:active::-moz-range-thumb {
  border-color: #ffffff;
}
input[type=range]:active::-ms-thumb {
  border-color: #ffffff;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  cursor: pointer;
  height: .75em;
  border-bottom: 2px solid rgba(255, 255, 255, 0.5);
  background-color: transparent;
}
input[type=range]::-moz-range-track {
  width: 100%;
  cursor: pointer;
  height: .75em;
  border-bottom: 2px solid rgba(255, 255, 255, 0.5);
  background-color: transparent;
}
input[type=range]::-ms-track {
  background: transparent;
  border-color: transparent;
  color: transparent;
}
.colors-section {
    padding-top: 3rem;
    margin-top: -3rem;
}

.title {
  margin: 4rem 0 1rem;
}

.colors-container {
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.colors-container h4 {
    margin-bottom: 1rem;
    flex: 1 1 100%;
}

.color-cell {
    margin-bottom: 2rem;
}

.color-text-block {
    display: grid;
    grid-template-columns: 1fr -webkit-min-content;
    grid-template-columns: 1fr min-content;
    /* gap: 1rem; */
    text-align: center;
}

.color-swatch {
    width: 100%;
    min-width: 200px;
    height: 150px;
    place-self: center;
}
#Harmonies {
    padding-top: 3rem;
    margin-top: 0rem;
}
